body{
    background-image: url('../../assets/bg\ Lendas.jpg');
    background-color: #121121;
    background-attachment: fixed;
    background-repeat: no-repeat;
}

.container{
    margin-left: 400px;
    max-width: 900px;
    height: 100vh;
    padding: 1.6em;
}

header{
    display: flex;
    justify-content: center;
    margin: auto;
    margin-bottom: 50px;
}

header h1{
    font-size: 4em;
}

main, section, footer{
    display: block;
    margin: auto;
    max-width: 700px;
    margin-bottom: 25px;
}
main center h1, main h1{
    margin-top: 50px;
    margin-bottom: 70px;
}

section strong{
    font-size: 1.2em;
    margin-bottom: 25px;
}

section a img{
    margin-top: 70px;
    width: 90px;
    height: 90px;
}

.links{
    margin-top: 70px;
    margin: auto;
    max-width: 300px;
    display: flex;
    justify-content: space-around;
}
footer{
    margin-top: 5vh;
}
